    public getServices(branchId:string):Observable<Service[]> {
        const cacheKey = `service_${branchId}`;
        const cached = this.cache.get<Service[]>(cacheKey);
    
        if (cached) {
          return of(cached);
        }
        return this.http.get(this.apiUrl + `/GetServiceList?branchId=${branchId}`, this.getRequestOption())
        .pipe(
            mergeMap((res:any) => {
                this.cache.set(cacheKey, res, 10); // TTL: 10 minutes
                return of(res);
            })
        );
    }
