    public checkUserValid() {
        return new Observable((observer: Subscriber<boolean>) => {
            this.http.get(this.apiUrl + '/checkLogin',
                this.getRequestOption())
                .subscribe(data => {
                    this.state.setGlobalSetting('LoggedIn', ["true"]);
                    observer.next(true);
                    observer.complete();
                }, Error => {
                    observer.next(false);
                    observer.complete();
                }, () => {
                    observer.complete();
                })
        })
    }

    postmaster(mode, dataToSave, postUrl) {

        let res = { status: "error", result: "" }
        let returnSubject: Subject<any> = new Subject();
        let opt = this.getRequestOption();
        let hd: Headers = new Headers({ 'Content-Type': 'application/json' });
        let op: RequestOptions = new RequestOptions()
        let bodyData = { mode: mode, data: dataToSave };
        //console.log(bodyData);
        this.http.post(this.apiUrl + postUrl, bodyData, this.getRequestOption())
            .subscribe((retData:any) => {
                if (retData.status == "ok") {
                    res.status = "ok";
                    res.result = retData.result
                    returnSubject.next(res);
                    returnSubject.unsubscribe();
                }
                else {
                    res.status = "error1"; res.result = retData.result;
                    //console.log(res);
                    returnSubject.next(res);
                    returnSubject.unsubscribe();
                }
            },
                error => {
                    res.status = "error2";
                    res.result = this.handleWebError(error);
                    //console.log(res);
                    returnSubject.next(res);
                    returnSubject.unsubscribe();
                }
            );
        return returnSubject;

    }

