    postform(mode, dataToSave, postUrl, files) {
        let res = { status: "error", result: "" }
        var formdata = new FormData();
        formdata.append("data", JSON.stringify(dataToSave));
        if (files != null) {
            for (let i = 0; i < files.length; i++) {
                formdata.append(files[i].name, files[i]);
            }
        }
        console.log(formdata, "formdata");
        let headers: HttpHeaders = new HttpHeaders({ 'Authorization': this.authService.getAuth().token });
        let returnSubject: Subject<any> = new Subject();
        this.http.post(this.apiUrl + postUrl, formdata, { headers: headers })
            .subscribe((retData:any) => {
                if (retData.status == "ok") {
                    res.status = "ok";
                    res.result = retData.result
                    returnSubject.next(res);
                    returnSubject.unsubscribe();
                }
                else {
                    res.status = "error1"; res.result = retData.result;
                    console.log(res);
                    returnSubject.next(res);
                    returnSubject.unsubscribe();
                }
            },
                error => {
                    res.status = "error2", res.result = error;
                    console.log(res);
                    returnSubject.next(res);
                    returnSubject.unsubscribe();
                }
            );
        return returnSubject;

    }
