    public TimeScheduleHeader(startingTime, closingTime) {
        let hourHeaderList = [];
        let noOfTimeHeaderInHour = 0;
        let totalNoOfMinCell = 0;
        let minList = [];

        var st = parseInt(startingTime.slice(0, -2));
        var stp = startingTime.slice(-2);
        var ct = parseInt(closingTime.slice(0, -2));
        var ctp = closingTime.slice(-2);
        if (ctp.toUpperCase() == "PM") { ct = ct + 12; }
        if (stp.toUpperCase() == "PM") { st = st + 12; }
        let starttime = st;
        let ap = stp.toUpperCase();
        for (let h = st; h < ct; h++) {
            let pred = "AM";
            if (h >= 12) { pred = "PM"; }

            hourHeaderList.push((h > 12 ? (h - 12) : h) + pred);
        }
        noOfTimeHeaderInHour = ct - st;
        totalNoOfMinCell = noOfTimeHeaderInHour * 4;
        for (let j = 1; j <= noOfTimeHeaderInHour; j++) {
            if (starttime >= 12) {
                ap = "PM";
            }
            for (let i = 0; i < 4; i++) {

                minList.push({ min: i * 15, booked: 0, startTime: starttime, minPart: i + 1, ampm: ap });
            }
            if (starttime == 12) { starttime = 0; ap = "PM" }
            starttime++;
        }
        return { hourHeaderList: hourHeaderList, minList: minList };

    }
    setBookedInCell(initialCellList, starttime, endtime, duration, room, service, customer, minuteCellWidth) {
        var s = starttime.slice(0, -5);
        var ampm = starttime.slice(-2);
        var onlyMin = parseInt(starttime.slice(-4).slice(0, -2));

        var noOfBLock = Math.ceil(duration / 15);
        console.log({ noOfBLock: noOfBLock });
        let isBooked = 0;
        let skip = 0;
        for (let t of initialCellList) {
            //console.log({Tstarttime:t.startTime, Tampm :t.ampm, starttime:s, ampm:ampm.toUpperCase(), onlyMin:onlyMin, minPart:t.minPart});
            if (t.startTime == s && t.ampm == ampm.toUpperCase()) {
                if (onlyMin == 0 && t.minPart == 1) {
                    t.booked = 1;
                    isBooked = 1;
                    t.colspan = noOfBLock;
                    skip = 1;
                    t.width = minuteCellWidth * noOfBLock + 'px';
                    t.room = room;
                    t.service = service;
                    t.customer = customer;
                }
                else if (onlyMin <= 15 && onlyMin > 0 && t.minPart == 2) {
                    t.booked = 1; isBooked = 1;
                    t.colspan = noOfBLock; skip = 1;
                    t.width = minuteCellWidth * noOfBLock + 'px';
                    t.room = room;
                    t.service = service;
                    t.customer = customer;
                }
                else if (onlyMin <= 30 && onlyMin > 15 && t.minPart == 3) {
                    t.booked = 1; isBooked = 1;
                    t.colspan = noOfBLock; skip = 1; t.width = minuteCellWidth * noOfBLock + 'px';
                    t.room = room;
                    t.service = service;
                    t.customer = customer;
                }
                else if (onlyMin <= 45 && onlyMin > 30 && t.minPart == 4) {
                    t.booked = 1; isBooked = 1;
                    t.colspan = noOfBLock; skip = 1; t.width = minuteCellWidth * noOfBLock + 'px';
                    t.room = room;
                    t.service = service;
                    t.customer = customer;
                }

            }
            if (isBooked == 1 && noOfBLock > 0) {
                if (skip == 1) { skip = 0; }
                else {

                    t.booked = 1;
                    t.cellToSplice = 1;
                }
                noOfBLock = noOfBLock - 1;
                if (noOfBLock == 0) { isBooked = 0; }

            }
        }
        return initialCellList;
    }

