    toBSDate(date:Date):string;
    toBSDate(date:string):string;

    public toBSDate(date: any){
        if(typeof date !== "string"){
            let adDate : string =`${date.getFullYear().toString()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2,'0')}`;
            return this.toBSDate(adDate)
        }
        else {
            var nepFunction = require('../../../assets/nepali-date-picker/nepali.datepicker.v3.5.min.js')
            var bsDate = nepFunction.ad2bs(date);
            return bsDate.year + '-' + bsDate.month.toString().padStart(2,'0') + '-' + bsDate.day.toString().padStart(2,'0');
        }
    }

    public toADDate(bsDate:string):string{
        var nepFunction = require('../../../assets/nepali-date-picker/nepali.datepicker.v3.5.min.js')
        var adDate = nepFunction.bs2ad(bsDate);
        return adDate.year + '-' + adDate.month.toString().padStart(2,'0') + '-' + adDate.day.toString().padStart(2,'0');
    }

    handleWebError(error):string{
        if(error.status === 404){
            return error.statusText;
        }
        else if(error._body){
            var eResponse = JSON.parse(error._body);
            if(eResponse && eResponse.result){
                return eResponse.result;
            }
        }
        return JSON.stringify(error);
    }  
