import { Component, ViewChild, OnInit } from '@angular/core';

import { GlobalState } from '../../../global.state';

import 'style-loader!./baPageTop.scss';
import { AuthService } from "../../../common/services/permission/authService.service";
import { Router } from "@angular/router";
import { MasterRepo } from '../../../common/repositories';
import { AppState } from '../../../app.service';

@Component({
  selector: 'ba-page-top',
  templateUrl: './baPageTop.html',
})
export class BaPageTop implements OnInit {
  @ViewChild('notificationAudio') notificationAudio: any;
  hasNewNotification = false;

  public isScrolled: boolean = false;
  public isMenuCollapsed: boolean = false;
  user: string;
  smsCredits: number;
  EnableTreatment: boolean;
  EnableAppointment: boolean;
  constructor(private _state: GlobalState
    , private _authService: AuthService
    , private appService: AppState
    , private router: Router
    , private service: MasterRepo) {
    var setting = _authService.getSetting();
    this.EnableTreatment = setting.EnableTreatment;
    this.EnableAppointment = setting.EnableAppointment;
    this._state.subscribe('menu.isCollapsed', (isCollapsed) => {
      this.isMenuCollapsed = isCollapsed;
    });
    if (_authService.getUserProfile()) {
      this.user = _authService.getUserProfile().UserId;
    }
    this.getSmsCredit();
  }

  ngOnInit(): void {
    this.appService.notification$.subscribe(data => {
      if(data.nType == 0){
        this.hasNewNotification = true;
        this.playNotificationSound();
      }
    });
  }

  playNotificationSound() {
    if (this.notificationAudio && this.notificationAudio.nativeElement) {
      this.notificationAudio.nativeElement.play().catch((err) => console.warn('Audio play failed:', err));
    }
  }

  clearNotification() {
    this.hasNewNotification = false;
  }

  getSmsCredit() {
    this.service.getSmsCredit().subscribe(
      (data:any) => {
        if(data.result)
          this.smsCredits = data.result.credits_available;  
      },

      (err) => console.log(err)
    )

  }

  public toggleMenu() {
    this.isMenuCollapsed = !this.isMenuCollapsed;
    this._state.notifyDataChanged('menu.isCollapsed', this.isMenuCollapsed);
    return false;
  }
  LogoutClick() {
    this._authService.removeAuth();
    this.router.navigate(['/login']);
  }
  public scrolledChanged(isScrolled) {
    this.isScrolled = isScrolled;
  }
}
